.content{
      width: 610px;
      height: 605px;
      background: var(--dark-100);
      position: relative;
      box-shadow: 1rem 1rem 20px rgba(0,0,0,.4), 3rem 3rem var(--theme), 0 0 80px rgba(0,0,0,.5);
      display: flex;
}

.content:before,
.content:after{
      width: 480px;
      height: 480px;
      border-radius: 100px;
      background: var(--theme);
}

.content:before{
      top: 0;
      left: 0;
      transform: translate(-120%, -50%) rotate(35deg);
}

.content:after{
      top: 100%;
      right: 0;
      transform: translate(120%, 0) rotate(-35deg);
}

.content_nav{
      width: 90px;
      height: 100%;
      background: var(--dark-100);
      position: relative;
      display: flex;
      align-items: center;
      z-index: 4;
}

.content_nav:before{
      width: 15px;
      height: 565px;
      background: var(--theme);
      top: 20px;
      left: 100%;
}

.nav_menu{
      position: relative;
      z-index: 2;
}

.nav_menu__box{
      width: 100px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;
}

.nav_menu__box li{
      width: 100px;
      height: 70px;
      background: transparent;
}

.nav_menu__box li.active{
      background: var(--grys);
}

.nav_menu__box li button{
      width: 90px;
      height: 70px;
      background: none;
      display: flex;
      align-items: center;
      justify-content: center;
}

.nav_menu__box li button *{
      color: var(--grys);
      font-size: 3.5rem;
      transition: .3s;
}

.nav_menu__box li.active button *,
.nav_menu__box li button:hover *{
      color: var(--theme);
}

.nav_menu__box li.current{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 70px;
      background: transparent;
      transition: all .3s ease-in-out;
      z-index: -1;
}

.nav_menu__box li:nth-child(1):hover ~ .current,
.current.home{
      top: 0;
      background: var(--grys);
}

.nav_menu__box li:nth-child(2):hover ~ .current,
.current.relation{
      top: calc(70px + 12px);
      background: var(--grys);
}

.nav_menu__box li:nth-child(3):hover ~ .current,
.current.video{
      top: calc(140px + 25px);
      background: var(--grys);
}

.nav_menu__box li:nth-child(4):hover ~ .current,
.current.gallery{
      top: calc(210px + 37px);
      background: var(--grys);
}

.nav_menu__box li:nth-child(5):hover ~ .current,
.current.friend{
      top: calc(280px + 50px);
      background: var(--grys);
}

.pages{
      width: 520px;
      height: 605px;
      background: var(--grys);
}

.page_content{
      width: 100%;
      height: 100%;
      position: relative;
      opacity: 0;
      visibility: hidden;
}

.page_content.show{
      animation: show_page .3s ease-in-out forwards;
}

@keyframes show_page{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.content_scroll{
      width: 100%;
      height: 100%;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--dark-200) var(--theme);
      padding-right: .2rem;
}