@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Fira+Mono:wght@400;500;700&family=Poppins:wght@200;300;400;500;600&family=Xanh+Mono&display=swap');

:root{
  /** fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Courier Prime', monospace;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red: #d90000;
  --alpha: rgba(0,0,0,.05);

  /** default theme */
  --theme: #eeaa74;
  --dark-200: #151d2f;
  --dark-100: #2b304d;
  --grys: #f5f5f4;

  /** globals */
  font-family: var(--ft-1);
  font-size: 62.5%;

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 920px;
  overflow: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--dark-200);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--dark-200);
}

::-webkit-scrollbar-thumb{
  background: var(--theme);
}