.content_video{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      gap: 2rem;
      color: var(--theme);
      position: relative;
      z-index: 2;
}

.content_video h2{
      width: 90%;
      text-align: center;
      font-size: 1.4rem;
}

.video_box{
      width: 400px;
      height: 236px;
      background: var(--dark-200);
      border-bottom: solid 1rem var(--grys);
      position: relative;
}

.video_box:before{
      width: 70%;
      height: 50px;
      border-radius: 50%;
      background: var(--dark-200);
      left: 15%;
      bottom: -25px;
      filter: blur(25px);
      opacity: .6;
      z-index: -1;
}

.video_box iframe{
      border: none;
}