.content_gallery{
      width: 100%;
      height: auto;
      overflow-x: hidden;
}

.gallery_row{
      width: 100%;
      height: auto;
      position: relative;
      overflow: hidden;
      margin-bottom: .1rem;
}

.gallery_row button{
      position: absolute;
      width: 50px;
      height: 50px;
      background: rgba(0,0,0,.5);
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      display: flex;
      align-items: center;
      justify-content: center;
}

.gallery_row button *{
      color: var(--light);
      font-size: 2.5rem;
}

.gallery_row:hover button{
      animation: pulse .3s ease-in-out forwards;
}

@keyframes pulse{
      0%{
            opacity: 0;
            transform: translate(-50%, -50%) scale(0);
      }
      70%{
            opacity: 1;
            transform: translate(-50%, -50%) scale(1.4);
      }
      100%{
            opacity: 1;
            transform: translate(-50%, -50%) scale(1);
      }
}