.banner{
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      left: 0;
}

.banner:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(-135deg, transparent, var(--dark-100));
      top: 0;
      left: 0;
}

.banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.page_content.show.home{
      display: flex;
      align-items: center;
      justify-content: center;
}

.wrapper_user{
      position: relative;
      width: 378px;
      height: 468px;
}

.wrapper_user:before,
.wrapper_user:after{
      height: 100px;
      background: rgba(255,255,255,.1);
      -webkit-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      left: 50%;
      transform: translatex(-50%);
      box-shadow: 0 -10px 10px rgba(0,0,0,.2);
      visibility: hidden;
}

.wrapper_user:before{
      top: .5rem;
      width: 258px;
}

.wrapper_user:after{
      top: 2rem;
      width: 318px;
}

.online_box{
      position: absolute;
      width: 77px;
      height: 84px;
      display: flex;
      flex-direction: column;
      align-items: center;
      top: 50px;
      left: 20px;
      z-index: 100;
}

.online_box .online{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      background: var(--grys);
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 1rem 0;
}

.online_box .online *{
      font-size: 2rem;
}

.online_box .online.green{
      box-shadow: 0 0 0 .5rem #39ca1136, 0 0 0 1rem #39ca1133;
}

.online_box .online.green *{
      color: #39ca11;
}

.online_box .online.red{
      box-shadow: 0 0 0 .5rem #d5101036, 0 0 0 1rem #d5101033;
}

.online_box .online.red *{
      color: #e31818;
}

.online_box span{
      text-transform: uppercase;
      font-weight: 500;
}

.user_data{
      width: 378px;
      height: 370px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      transform: translateY(3.8rem);
      background: var(--grys);
      z-index: 2;
      box-shadow: 0 0 20px rgba(0,0,0,.3);
}

.user_data:before{
      width: 50%;
      height: 1rem;
      background: var(--dark-200);
      left: 0;
      top: 0;
}

.user_data__avatar{
      width: 150px;
      height: 150px;
      position: absolute;
      top: -4.4rem;
      left: calc(50% - 75px);
      padding: .5rem;
      background: linear-gradient(transparent, var(--theme));
      box-shadow: 20px 20px 30px rgba(0,0,0,.3);
}

.user_data__avatar img{
      width: 140px;
      height: 140px;
}

.user_data__info{
      width: 100%;
      margin-top: 120px;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: var(--dark-200);
}

.user_data__info h1{
      font-size: 2rem;
}

.user_data__info p{
      font-size: 1.2rem;
      text-align: center;
      width: 80%;
}

.user_data__info strong{
      font-size: 1.6rem;
}

.user_data_descrition{
      width: 80%;;
      height: 110px;
      overflow: hidden;
}

.user_data_descrition p{
      font-size: 1.2rem;
      text-align: center;
}

.user_page{
      width: 134px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 1rem;
}

.user_page a{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      background: rgba(0,0,0,.05);
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_page a *{
      font-size: 2rem;
      color: var(--theme);
}

.social{
      position: relative;
      width: 348px;
      height: 58px;
      padding: 1rem;
      margin-top: 4rem;
      margin-left: auto;
      margin-right: auto;
}

.social_menu{
      position: relative;
      width: 100%;
      display:flex;
      justify-content: space-between;
      align-items: center;
}

.social_menu:after{
      width: 348px;
      height: 100px;
      bottom: -8px;
      left: 50%;
      transform: translateX(-50%);
      background: var(--theme);
}

.social_menu *{
      position: relative;
      z-index: 2;
}