.btn_social{
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      transition: .2s;
      flex-shrink: 0;
      background: rgba(0,0,0,.1);
}

.btn_social *{
      color: var(--light);
      font-size: 1.6rem;
}

.btn_social:hover *{
      color: var(--dark-100);
}