.relation_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      box-shadow: 0 215px var(--dark-100) inset;
}


.relation_avatar{
      width: 150px;
      height: 150px;
      padding: .5rem;
      background: linear-gradient(transparent, var(--theme));
      box-shadow: 20px 20px 30px rgba(0,0,0,.2);
      margin-top: 140px;
}

.relation_avatar img{
      width: 140px;
      height: 140px;
}

.relation_data{
      width: 80%;
      margin-top: 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: var(--dark-200);
}

.relation_data h2{
      font-size: 2rem;
      color: var(--theme);
}

.relation_data strong{
      font-size: 1.6rem;
}

.relation_data p{
      font-size: 1.2rem;
      text-align: center;
}

.relation_data a{
      text-transform: uppercase;
      padding: .3rem 1rem;
      border-radius: 999px;
      background: var(--theme);
      color: var(--dark-200);
      margin-top: 1.5rem;
}