.empty_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 2rem;
}

.empty_content *{
      color: rgba(0, 0, 0, .5);
      font-size: 2rem;
}

.ghost_icon *{
      font-size: 6rem;
}

.empty_content h2{
      color: rgba(0,0,0,.2);
}