.friends_col{
      width: 280px;
      height: auto;
      min-height: 100%;
      margin-left: calc(100% - 280px);
      display: flex;
      flex-direction: column;
      gap: .1rem;
      background: rgba(0,0,0,.01);
}

.friend_row{
      width: 100%;
      height: 70px;
      padding-right: .5rem;
      display: grid;
      grid-template-columns: 70px 1fr 30px;
      gap: .5rem;
      align-items: center;
      background: var(--dark-100);
      color: var(--theme);
}

.friend_row img{
      width: 70px;
      height: 70px;
}

.friend_data{
      height: 100%;
      overflow: hidden;
      padding: .5rem 0;
}

.friend_data strong{
      font-size: 1.4rem;
      font-weight: 700;
}

.friend_data p{
      font-size: 1.1rem;
      color: var(--grys);
}

.friend_row a{
      width: 30px;
      height: 30px;
      background: var(--dark-200);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .5s;
}

.friend_row a:hover{
      transform: rotate(360deg);
}

.friend_row a *{
      color: var(--theme);
      font-size: 2rem;
}